

.header-main {
	height: $header-main-height;
	border-bottom: solid thin $light-border-colour;
	background-color: $blackish;

	position: fixed;
	width: 100%;
	z-index: 500;
	top: 0;
	left: 0;
	right: 0;
	bottom: auto;
	.header-main-inner {
		height: $header-main-height;
	}
	.l-row {
		display: flex;
		justify-content: space-between;
	}
	.header-main__site-logo {
		height: $header-main-height;
		@include flexVerticalCenter;
	}

}

.header-main__site-logo-img {
	max-width: 50px;
}
.header-main__navigation {
	height: $header-main-height;
}

